import React from "react";
import '../Css/Home1.scss';

import {useSelector,useDispatch} from 'react-redux';
import {bindActionCreators} from "redux";
import {actioncreator} from "../State/index";
import { setState } from '../State/action-creators';
import  c2 from '../Assets/c2.jpg';
import  c1 from '../Assets/c1.png';
import  c3 from '../Assets/c3.jpg';
import  c4 from '../Assets/c4.jpg';


import {Carousel, Container, NavDropdown, Row, Form,Nav,Col } from "react-bootstrap";

function Home1() {

    const state = useSelector((state) => state);
    const dispatch = useDispatch();
    const action = bindActionCreators(actioncreator,dispatch);

    return <div className="main">
        
        <Carousel  prevLabel='' nextLabel='' indicators={false}>
  <Carousel.Item  className="carousel" interval={5000}>
    <img
      className="d-block w-100"
      src={c1}
      alt="First slide"
    />
    <Carousel.Caption>
      <h3>First slide label</h3>
      <p>Nulla vitae elit libero, a pharetra augue mollis interdum.</p>
    </Carousel.Caption>
  </Carousel.Item>
  <Carousel.Item className="carousel" interval={5000}>
    <img
      className="d-block w-100"
      src={c3}
      alt="Second slide"
    />
    <Carousel.Caption>
      <h3>Second slide label</h3>
      <p>Lorem ipsum dolor sit amet, consectetur adipiscing elit.</p>
    </Carousel.Caption>
  </Carousel.Item>
  <Carousel.Item className="carousel" interval={5000}>
    <img
      className="d-block w-100"
      src={c4}
      alt="Third slide"
    />
    <Carousel.Caption>
      <h3>Third slide label</h3>
      <p>Praesent commodo cursus magna, vel scelerisque nisl consectetur.</p>
    </Carousel.Caption>
  </Carousel.Item>
</Carousel>
<Container className="welcome">Welcome to Connecting Lives</Container>
<Container className="motto1">"                   ! <br/>-  "</Container>
{false && <Container className="motto1">"Be someone's light when they are hopeless."</Container>}

    </div>

}

export default Home1;